<template lang="html">
  <el-col :span="col" :id="ivpid" class="ivp-col">
    <span style="color: transparent;">{{ info }}</span>
    <div class="ivp-table line" ref="ivp">
      <div class="ivp-table-box line-box" :style="{ fontSize: col*4 +'px' }">
        <div class="line-box-line" :style="{ backgroundColor: color[lColor], height: col*3+'px' }"></div>
      </div>
    </div>
  </el-col>
</template>

<script>
export default {
  data () {
    return {
      formLabelWidth: '80px',
      backgroundColor: ['rgba(144, 164, 174, 0.5)', '#cfecfe', 'rgba(255, 110, 110, 0.5)'],
      color: ['#90a4ae', '#11a0f8', '#ff6e6e']
    }
  },
  props: {
    ivpid: {
      default: null
    },
    col: {
      type: Number,
      default: 5
    },
    info: {
      default: 'information'
    },
    lColor: {
      type: Number,
      default: 0
    }
  }
}
</script>

<style lang="css">
.ivp-col {
  min-width: 100px !important;
}
.line-box {
  display: flex;
  margin: 0 -21px;
  justify-content: center;
  padding-left: 0;
  padding-right: 0;
  align-items:center;
  background-color: transparent;
}
.ivp-table.line {
  background-color: transparent;
}
.line-box-line {
  width: 100%;
}
.ivp-table-box {
  position: absolute;
  padding: 1.5% 0.5%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.ivp-table .el-col{
  position: relative;
  display: block;
  height: 33.33%;
  padding: 1.6% 1.8%;
}
.ivp-cell {
  border-radius: 4px;
  background-color: #11a0f8;
  height: 100%;
  color: rgba(255, 255, 255, .8);
  font-size: 1em;
  display: flex;
  justify-content: center;
  align-items:center;
}
.ivp-more {
  display: block;
  color: #b3c2ca;
  cursor: pointer;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -12px;
  margin-left: -12px;
  font-size: 24px;
}
.ivp-more:hover {
  color: #566062;
}
</style>



