<template lang="html">
  <el-col v-show="show" :span="col" class="ivp-col">
    <span>Add IVP</span>
    <div class="ivp-add-table" ref="ivp" @click="dialogFormVisible = true">
      <div class="ivp-table-box ivp-add-box">
        <i class="el-icon-plus ivp-add-icon" :style="{ fontSize: size }"></i>
      </div>
    </div>
    <el-dialog
      title="Add IVP"
      v-model="dialogFormVisible"
      @open="open">
      <div class="addition-form"
      v-loading="loading"
      element-loading-text="committing"
      >
        <el-form ref="form" :model="form" label-width="90px">
          <el-form-item label="Device IP">
            <el-input v-model="form.ip"></el-input>
          </el-form-item>
          <el-form-item label="Contact Person">
            <el-input v-model="form.user"></el-input>
          </el-form-item>
          <el-form-item label="Contact Number">
            <el-input v-model="form.phone"></el-input>
          </el-form-item>
          <el-form-item label="Address">
            <el-input v-model="form.address"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">Submit</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </el-col>
</template>

<script>
import Bus from '../router/bus.js'
import axiosConfig from '../router/axios.config.js'
export default {
  data () {
    return {
      loading: false,
      show: false,
      dialogFormVisible: false,
      formLabelWidth: '80px',
      form: {
        ip: '',
        user: '',
        phone: '',
        address: ''
      }
    }
  },
  props: {
    col: {
      type: Number,
      default: 5
    },
    size: {
      default: function () {
        var size = '15px'
        return size
      }
    }
  },
  created: function () {
    Bus.$on('left-show', this.click_show)
  },
  methods: {
    click_show () {
      if (this.show === false) {
        this.show = true
      } else {
        this.show = false
      }
    },
    open () {
      this.form.ip = ''
      this.form.user = ''
      this.form.phone = ''
      this.form.address = ''
      this.loading = false
    },
    onSubmit () {
      if (this.form.ip === '') {
        this.$message({
          showClose: true,
          message: 'Please fill in IP',
          type: 'error'
        })
        return
      }
      this.loading = true
      this.$http.post('/ivps', {
        'ip': this.form.ip,
        'user': this.form.user,
        'phone': this.form.phone,
        'addressofdevice': this.form.address
      }, axiosConfig)
      .then(response => {
        this.loading = false
        if (response.data.errorcode === 1) {
          this.$message({
            showClose: true,
            message: 'Success',
            type: 'success'
          })
          this.form.ip = ''
          this.form.user = ''
          this.form.phone = ''
          this.form.address = ''
        } else {
          this.$message({
            showClose: true,
            message: 'server error:' + response.status,
            type: 'error'
          })
        }
      })
      .catch(error => {
        this.loading = false
        this.$message({
          showClose: true,
          message: error.response.status,
          type: 'error'
        })
      })
    }
  }
}
</script>

<style lang="css">
.ivp-add-table {
  display: block;
  position: relative;
  margin: 15px 10px;
  margin-top: 5px;
  background-color: #f1f1f1;
  border-radius: 7px;
  padding-bottom: 31%;
}
.ivp-add-box {
  padding-top: 10%;
}
.ivp-add-icon {
  color: #D8D8D8;
}
</style>



