<template lang="html">
  <div class="stream-line">
    <template
      v-for="(device, index) in devices"
    >
      <comp-line
        v-if="device.line[0]"
        :col="col"
        :lColor="device.line[1]"
      >
      </comp-line>
      <comp-ivp
        :col="col"
        :sColor="device.slot"
        :iColor="device.device"
        :info="device.ip"
        :slotStatus="device.name"
      >
      </comp-ivp>
    </template>
  </div>
</template>

<script>
import CompIvp from '../../../components/ivp.vue'
import CompLine from '../../../components/line.vue'
export default {
  components: {
    CompIvp,
    CompLine
  },
  data () {
    var num = 6
    if (parseInt(24 / (this.ivpnum * 2 - 1)) > 6) {
      num = 6
    } else {
      num = parseInt(24 / (this.ivpnum * 2 - 1))
    }
    return {
      col: num
    }
  },
  props: {
    ivpnum: {
      type: Number,
      default: 3
    },
    devices: {
      type: Array,
      default: function (value) {
        return [
          {'ip': '111', 'device': 2, 'line': [false, 0], 'slot': [0, 0, 0, 0, 0, 0, 0], 'name': ['', '', '', '', '', '', '']},
          {'ip': '113', 'device': 1, 'line': [true, 0], 'slot': [0, 0, 2, 0, 0, 0, 0], 'name': ['', '', '', '', '', '', '']},
          {'ip': '114', 'device': 0, 'line': [true, 0], 'slot': [0, 0, 0, 0, 0, 0, 0], 'name': ['', '', '', '', '', '', '']}
        ]
      }
    }
  }
}
</script>

<style lang="css">
.stream-line {
  width: 100%;
  float: left;
}
</style>



