<template lang="html">
  <transition name="slide-fade">
    <el-menu v-show="show" default-active="2" class="el-menu-vertical-demo left-bar">
      <el-form class="left-form" ref="form" :model="form1" label-width="180px">
        <span class="left-name">IVP</span>
        <el-form-item label="Refresh Frequency:">
          <div class="left-input">
            <el-input
              size="small"
              v-model="form1.second">
              <template slot="append">S</template>
            </el-input>
          </div>
        </el-form-item>
        <el-form-item label="Display Mode:" label-width="166px">
          <div class="left-select">
            <el-select
              v-model="form1.type"
              size="small"
              placeholder="">
              <el-option label="a" value="shanghai"></el-option>
              <el-option label="b" value="beijing"></el-option>
              <el-option label="c" value="beijing1"></el-option>
            </el-select>
          </div>
        </el-form-item>
        <el-form-item label-width="0">
          <el-button type="primary" @click="onSubmit_a">Submit</el-button>
        </el-form-item>
      </el-form>
      <el-form class="left-form" ref="form" :model="form2" label-width="180px">
        <span class="left-name">Streams</span>
        <el-form-item label="Refresh Frequency:">
          <div class="left-input">
            <el-input
              size="small"
              v-model="form2.second">
              <template slot="append">S</template>
            </el-input>
          </div>
        </el-form-item>
        <el-form-item label="Display Mode:" label-width="166px">
          <div class="left-select">
            <el-select
              v-model="form2.type"
              size="small"
              placeholder="">
              <el-option label="a" value="shanghai"></el-option>
              <el-option label="b" value="beijing"></el-option>
              <el-option label="c" value="beijing"></el-option>
            </el-select>
          </div>
        </el-form-item>
        <el-form-item label-width="0">
          <el-button type="primary" @click="onSubmit_b">Submit</el-button>
        </el-form-item>
      </el-form>
    </el-menu>
  </transition>
</template>

<script>
import Bus from '../router/bus.js'
export default {
  data () {
    return {
      show: false,
      form1: {
        second: '',
        type: ''
      },
      form2: {
        second: '',
        type: ''
      }
    }
  },
  created: function () {
    Bus.$on('left-show', this.click_show)
  },
  methods: {
    onSubmit_a () {
      console.log('submit!')
    },
    onSubmit_b () {
      console.log('submit!')
    },
    click_show () {
      if (this.show === false) {
        this.show = true
      } else {
        this.show = false
      }
    }
  }
}
</script>

<style lang="css">
.left-bar {
  position: relative;
  display: block;
  overflow: hidden;
  flex-grow: 0;
  height: 100%;
  min-width: 350px;
  width: 350px;
  border-radius: 0;
  background-color: #566062 !important;
}
.left-name {
  display: block;
  color: #fff;
  font-size: 24px;
  margin-bottom: 20px;
}
.left-form {
  width: 78%;
  margin: 0 auto;
  margin-top: 80px;
}
.left-form label{
  font-size: 18px;
  text-align: left;
  color: #fff;
}
.left-input {
  width: 85px;
}
.left-select {
  width: 100px;
}
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .2s ease;
}
.slide-fade-enter, .slide-fade-leave-active {
  transform: translateX(-15px);
  opacity: 0;
}
</style>



