<template lang="html">
  <div class="show" v-loading="loading">
    <comp-ivpadd
      :col="col_size"
    ></comp-ivpadd>
    <comp-ivp
      v-for="(ivp, index) in ivps"
      :ivpid="ivp.id"
      :info="ivp.ip"
      :col="col_size"
      :slotStatus="ivp.slot_name"
      :sColor="ivp.slot_status"
    >
    </comp-ivp>
  </div>
</template>

<script>
import CompIvp from '../../components/ivp.vue'
import CompIvpadd from '../../components/ivpAdd.vue'
import axiosConfig from '../../router/axios.config.js'
export default {
  components: {
    CompIvp,
    CompIvpadd
  },
  data () {
    return {
      loading: true,
      col_size: 4,
      ivps: []
    }
  },
  mounted () {
    this.get_ivp_list()
  },
  methods: {
    get_ivp_list () {
      this.$http.get('/ivps/allpos', axiosConfig)
      .then(response => {
        this.loading = false
        if (response.data.errorcode === 0) {
          for (var i = 0; i < response.data.ivp_list.length; i++) {
            var slotNameList = ['', '', '', '', '', '', '']
            var slotStatusList = [0, 0, 0, 0, 0, 0, 0]
            for (var x = 0; x < response.data.ivp_list[i].slot_list.length; x++) {
              var slotJson = response.data.ivp_list[i].slot_list[x]
              var slot = 'slot' + x
              slotNameList[x] = (slotJson[slot].name).toUpperCase()
              if (slotJson[slot].status !== '') {
                if (slotJson[slot].status === 'Ready') {
                  slotStatusList[x] = 1
                }
                if (slotJson[slot].status === 'Bad') {
                  slotStatusList[x] = 2
                }
              } else {
                slotStatusList[x] = 0
              }
            }
            this.ivps.push({
              num: i,
              id: response.data.ivp_list[i].id,
              ip: response.data.ivp_list[i].ip,
              slot_name: slotNameList,
              slot_status: slotStatusList
            })
          }
        } else {
          this.$message({
            showClose: true,
            message: 'error(server): ' + response.data.errorcode,
            type: 'error'
          })
        }
      })
      .catch(error => {
        this.loading = false
        this.$message({
          showClose: true,
          message: 'error: ' + error.response.status,
          type: 'error'
        })
      })
    }
  }
}
</script>

<style lang="css">
  .show {
    position: relative;
    flex-grow: 1;
    overflow-y:scroll;
  }
  .ivp-col {
    min-width: 100px;
  }
</style>



