<template>
  <div id="app">
    <comp-bar></comp-bar>
    <div class="main">
      <comp-left></comp-left>
      <transition name="el-fade-in-linear">
        <router-view></router-view>
      </transition>
    </div>
  </div>
</template>

<script>
import CompBar from './components/bar.vue'
import CompLeft from './components/left.vue'
export default {
  name: 'app',
  components: {
    CompBar,
    CompLeft
  }
}
</script>

<style>
* {
  box-sizing: border-box;
}
html,body,#app {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: #f4f8f9;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #566062;
}
.main {
  position: absolute;
  top: 64px;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  display: -webkit-flex;
}
</style>



