import Vue from 'vue'
import Router from 'vue-router'
import Devices from '@/views/devices/devices.vue'
import Streams from '@/views/streams/streams.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/devices',
      name: 'devices',
      component: Devices
    },
    {
      path: '/streams',
      name: 'streams',
      component: Streams
    }
  ]
})



