<template lang="html">

</template>

<script>
import axiosConfig from '../router/axios.config.js'
export default {
  data () {
    return {
      time: 5000,
      keepIvpFunc: null,
      keepStreamFunc: null
    }
  },
  mounted () {
    this.keep_ivp_alive()
    this.keep_stream_alive()
  },
  methods: {
    keep_ivp () {
      this.$http.get('/ivps', axiosConfig)
      .then(response => {
        if (response.data.errorcode === 0) {
        }
      })
      .catch(error => {
        clearInterval(this.keepIvpFunc)
        this.$notify.error({
          title: 'Error(IVP): ' + error.response.status,
          message: 'Connection failed. Please try to refresh the page or contact the administrator.',
          duration: 0
        })
      })
      if (this.$route.path === '/devices') {
        console.log(1)
      }
      if (this.$route.path === '/streams') {

      }
    },
    keep_stream () {
      this.$http.get('/ivps/dd', axiosConfig)
      .then(response => {
        console.log(1)
      })
      .catch(error => {
        clearInterval(this.keepStreamFunc)
        this.$notify.error({
          title: 'Error(Streams): ' + error.response.status,
          message: 'Connection failed. Please try to refresh the page or contact the administrator.',
          duration: 0
        })
      })
      if (this.$route.path === '/devices') {
        console.log(1)
      }
      if (this.$route.path === '/streams') {

      }
    },
    keep_ivp_alive () {
      clearInterval(this.keepIvpFunc)
      var that = this
      this.keepIvpFunc = setInterval(() => {
        this.keep_ivp()
      }, that.time)
    },
    keep_stream_alive () {
      clearInterval(this.keepStreamFunc)
      var that = this
      this.keepStreamFunc = setInterval(() => {
        this.keep_stream()
      }, that.time)
    }
  }
}
</script>

