<template lang="html">
  <el-col :span="col" :id="ivpid" class="ivp-col">
    <span>{{ info }}</span>
    <div class="ivp-table" ref="ivp" :style="{ backgroundColor: backgroundColor[iColor] }">
      <div class="ivp-table-box" :style="{ fontSize: col*4 +'px' }">
        <el-col :span="16">
          <div class="ivp-cell" :style="{ backgroundColor: color[sColor[6]]}">
            {{ slotStatus[6] }}
          </div>
        </el-col>
        <el-col :span="8">
          <i class="el-icon-more ivp-more" @click="dialogFormVisible = true"></i>
        </el-col>
        <el-col :span="8">
          <div class="ivp-cell" :style="{ backgroundColor: color[sColor[3]]}">
            {{ slotStatus[3] }}
          </div>
        </el-col>
        <el-col :span="8">
          <div class="ivp-cell" :style="{ backgroundColor: color[sColor[4]]}">
            {{ slotStatus[4] }}
          </div>
        </el-col>
        <el-col :span="8">
          <div class="ivp-cell" :style="{ backgroundColor: color[sColor[5]]}">
            {{ slotStatus[5] }}
          </div>
        </el-col>
        <el-col :span="8">
          <div class="ivp-cell" :style="{ backgroundColor: color[sColor[0]]}">
            {{ slotStatus[0] }}
          </div>
        </el-col>
        <el-col :span="8">
          <div class="ivp-cell" :style="{ backgroundColor: color[sColor[1]]}">
            {{ slotStatus[1] }}
          </div>
        </el-col>
        <el-col :span="8">
          <div class="ivp-cell" :style="{ backgroundColor: color[sColor[2]]}">
            {{ slotStatus[2] }}
          </div>
        </el-col>
      </div>
    </div>
    <el-dialog
      title="IVP Status"
      v-model="dialogFormVisible">

    </el-dialog>
  </el-col>
</template>

<script>
export default {
  data () {
    return {
      dialogFormVisible: false,
      formLabelWidth: '80px',
      backgroundColor: ['rgba(144, 164, 174, 0.5)', '#cfecfe', 'rgba(255, 110, 110, 0.5)'],
      color: ['#90a4ae', '#11a0f8', '#ff6e6e']
    }
  },
  props: {
    ivpid: {
      default: null
    },
    sColor: {
      type: Array,
      default: function (value) {
        return [0, 0, 0, 0, 0, 0, 0]
      }
    },
    iColor: {
      type: Number,
      default: 0
    },
    col: {
      type: Number,
      default: 5
    },
    info: {
      default: 'information'
    },
    slotStatus: {
      type: Array,
      default: function (value) {
        return ['', '', '', '', '', '', '']
      }
    }
  }
}
</script>

<style lang="css">
.ivp-col {
  min-width: 100px !important;
}
.ivp-table {
  display: block;
  position: relative;
  margin: 15px 10px;
  margin-top: 5px;
  background-color: #cfecfe;
  border-radius: 7px;
  padding-bottom: 31%;
  font-size: 16px;
}
.ivp-table-box {
  position: absolute;
  padding: 1.5% 0.5%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.ivp-table .el-col{
  position: relative;
  display: block;
  height: 33.33%;
  padding: 1.6% 1.8%;
}
.ivp-cell {
  border-radius: 4px;
  background-color: #11a0f8;
  height: 100%;
  color: rgba(255, 255, 255, .8);
  font-size: 1em;
  display: flex;
  justify-content: center;
  align-items:center;
}
.ivp-more {
  display: block;
  color: #b3c2ca;
  cursor: pointer;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -12px;
  margin-left: -12px;
  font-size: 24px;
}
.ivp-more:hover {
  color: #566062;
}
</style>



