<template lang="html">
<el-menu theme="dark" class="top-bar" mode="horizontal">
  <i @click="click_left" class="el-icon-menu icon-menu"></i>
  <div class="top-search">
    <el-input
      class="top-search"
      placeholder="Search IVP, Streams"
      icon="search"
      max="450px"
      v-model="input"
      :on-icon-click="handleIconClick">
    </el-input>
  </div>
  <div class="top-switch">
    IVP
    <el-switch
      v-model="value1"
      width = "55"
      @change = "change_url"
      on-text=""
      off-text="">
    </el-switch>
    Streams
  </div>
  <comp-keep></comp-keep>
</el-menu>
</template>

<script>
import Bus from '../router/bus.js'
import CompKeep from './keepAlive.vue'
export default {
  components: {
    CompKeep
  },
  data () {
    return {
      value1: false
    }
  },
  mounted () {
    if (this.$route.path === '/devices') {
      this.value1 = false
    }
    if (this.$route.path === '/streams') {
      this.value1 = true
    }
  },
  methods: {
    click_left: function (id) {
      Bus.$emit('left-show', 2)
    },
    change_url () {
      if (this.value1 === false) {
        this.$router.push({path: '/devices'})
      } else {
        this.$router.push({path: '/streams'})
      }
    }
  }
}
</script>

<style lang="css">
  .top-bar {
    z-index: 1000;
    background-color: #fff !important;
    height: 64px !important;
    width: 100%;
    text-align: center;
    line-height: 64px;
  }
  .icon-menu {
    margin-top: 24px;
    margin-left: 40px;
    float: left;
    cursor: pointer;
    color: #90A4AE;
  }
  .top-search {
    margin: auto;
    width: 100%;
    max-width: 550px;
  }
  .top-switch {
    position: absolute;
    top: 50%;
    margin-top: -10px;
    right: 35px;
    float: right;
    line-height: 0;
  }
</style>



