<template lang="html">
  <div class="show" v-loading="loading">
    <comp-stream
      :ivpnum="stream.length"
      :devices="stream"
      v-for="(stream, index) in streams"
    ></comp-stream>
  </div>
</template>

<script>
import CompStream from './components/stream.vue'
import axiosConfig from '../../router/axios.config.js'
export default {
  components: {
    CompStream
  },
  data () {
    return {
      loading: true,
      streams: []
    }
  },
  mounted () {
    this.get_stream_list()
  },
  methods: {
    get_stream_list () {
      this.$http.get('/stream', axiosConfig)
      .then(response => {
        this.loading = false
        var json = response.data
        if (json.errorcode === '200') {
          for (var y = 0; y < json.linklist.length; y++) {
            var streamDevice = []
            var lineType = true
            var lineStatus = 0
            if (json.linklist[y].status === 'running') {
              lineStatus = 1
            } else {
              lineStatus = 2
            }
            for (var i = 0; i < json.linklist[y].device_list.length; i++) {
              if (i === 0) {
                lineType = false
              } else {
                lineType = true
              }
              var slotList = [0, 0, 0, 0, 0, 0, 0]
              var nameList = ['', '', '', '', '', '']
              for (var x = 0; x < json.linklist[y].device_list[i].board_list.length; x++) {
                var name = ''
                var position = 0
                if (json.linklist[y].device_list[i].board_list[x].position.replace(/[^a-z]/ig, '') === 'ge') {
                  position = 6
                  name = json.linklist[y].device_list[i].board_list[x].name + ' (' + json.linklist[y].device_list[i].board_list[x].position + ')'
                } else {
                  position = json.linklist[y].device_list[i].board_list[x].position.replace(/[^0-9]/ig, '')
                  name = json.linklist[y].device_list[i].board_list[x].name
                }
                nameList[position] = name
                if (json.linklist[y].device_list[i].board_list[x].status === 'ready') {
                  slotList[position] = 1
                }
                if (json.linklist[y].device_list[i].board_list[x].status === 'bad') {
                  slotList[position] = 2
                }
              }
              streamDevice.push(
                {
                  'ip': json.linklist[y].device_list[i].ip,
                  'line': [lineType, lineStatus],
                  'slot': slotList,
                  'name': nameList
                }
              )
            }
            this.streams.push(streamDevice)
          }
        } else {
          this.$message({
            showClose: true,
            message: 'error(server): ' + response.data.errorcode,
            type: 'error'
          })
        }
      })
      .catch(error => {
        this.loading = false
        this.$message({
          showClose: true,
          message: 'error: ' + error.response.status,
          type: 'error'
        })
      })
    }
  }
}
</script>

<style lang="css">
.show {
  position: relative;
  flex-grow: 1;
}
</style>



